import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/atestado';

const AtestadoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'nrAtestado',
            text: 'Número Atestado',
          },
          {
            dataField: 'dtEmissao',
            text: 'DT.Emissão',
          },
          {
            dataField: 'medicoEmitente',
            text: 'Emitente',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'AtestadoSearchView',
      }}
    />
  );
};

export default AtestadoIS;
