import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Button, Col, Row, UncontrolledTooltip } from 'reactstrap';
import {
  add,
  Box,
  Checkbox,
  compare,
  DetailEditForm,
  DetailEditViewFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  toDate,
} from 'summer';
import { VinculoMovimentacao } from '../../../../models/dtos/mfp/vinculoMovimentacao';
import {
  AFASTAMENTO_ENUM_VALUES,
  AFASTAMENTO_SUSPENSAO_CONTRATO_TRABALHO,
  AFASTAMENTO_TEMP_ABORTO_NAO_CRIMINOSO,
  AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_INF_15DIAS,
  AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_SUP_15_DIAS,
  AFASTAMENTO_TEMP_DOENCA_PER_IG_INF_15DIAS,
  AFASTAMENTO_TEMP_DOENCA_PER_SUP_15DIAS,
  AFASTAMENTO_TEMP_LICENCA_MATERNIDADE,
  AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA,
  AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_1ANO,
  AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_4ANOS,
  AFASTAMENTO_TEMP_MANDATO_SINDICAL,
  AFASTAMENTO_TEMP_SERVICO_MILITAR,
  APOSENTADORIA_INVALIDEZ,
  LICENCA_PERCEPCAO_SALARIO,
  LICENCA_SEM_PERCEPCAO_SALARIO,
  MOTIVO_AFASTAMENTO_VALUES,
  MOTIVO_RETORNO_VALUES,
  NOVO_AFASTAMENTO_MESMO_ACIDENTE,
  NOVO_AFASTAMENTO_TEMP_MESMA_DOENCAO_DENTRO_60DIAS,
  PRORROGACAO_AFASTAMENTO_TEMP_LICENCA_MATERNIDADE,
  PRORROGACAO_DURACAO_LICENCA_MATERNIDADE,
  RETORNO_AFASTAMENTO_SUSPENSAO_CONTRATO_TRABALHO,
  RETORNO_AFASTAMENTO_TEMPORARIO_ABORTO_NAO_CRIMINOSO_Q3,
  RETORNO_AFASTAMENTO_TEMPORARIO_ACIDENTE_TRABALHO_IGUAL_MENOR_15_DIAS,
  RETORNO_AFASTAMENTO_TEMPORARIO_ACIDENTE_TRABALHO_MAIOR_15_DIAS,
  RETORNO_AFASTAMENTO_TEMPORARIO_DOENCA_MAIOR_15_DIAS,
  RETORNO_AFASTAMENTO_TEMPORARIO_DOENCAO_IGUAL_MENOR_15_DIAS,
  RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE,
  RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q4,
  RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q5,
  RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q6,
  RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q7,
  RETORNO_AFASTAMENTO_TEMPORARIO_MANDATO_SINDICAL,
  RETORNO_AFASTAMENTO_TEMPORARIO_MESMA_DOENCA,
  RETORNO_AFASTAMENTO_TEMPORARIO_MESMO_ACIDENTE_TRABALHO,
  RETORNO_AFASTAMENTO_TEMPORARIO_SERVICO_MILITAR,
  RETORNO_APOSENTADORIA_INVALIDEZ,
  RETORNO_LICENCA_COM_PERCEPCAO_SALARIO,
  RETORNO_LICENCA_SEM_PERCEPCAO_SALARIO,
  RETORNO_PRORROGACAO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE,
  RETORNO_SUSPENSAO_TEMPORARIA_DO_CONTRATO_TRABALHO_MP936_20,
  SUSPENSAO_TEMPORARIA_DO_CONTRATO_TRABALHO_MP936_20,
} from '../../../../models/enumerated/mfp/afastamentoEnum';
import { SEM_VINCULO_ESOCIAL_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';
import { EVT_NP_ALTERACAO_CONTRATUAL, EVT_NP_TRAB_SEM_VINC_ALTERACAO } from '../../../../models/enumerated/mfp/eSocialEventoEnum';
import { verificarSituacaoAfastamento } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import EsocialUtil from '../../../../utilities/esocial.util';
import { condicional, getItemAtual, promisedAlert } from '../../../../utilities/vinculo.util';
import AtestadoIS from '../../../components/mfp/atestadoIS/AtestadoIS';

interface ScreenFields {
  dataAfastamentoFmt?: string;
  dataFimAfastamentoFmt?: string;
  dataRetornoFmt?: string;
}

const VinculoMovimentacaoEditView: FC<DetailEditViewFormProps<VinculoMovimentacao>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<VinculoMovimentacao & ScreenFields>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const [modoTela, setModoTela] = useState('normal');
  const [podeGerarEventoPendente, setPodeGerarEventoPendente] = useState(false);
  const [tooltipMotivoAfastamento, setTooltipMotivoAfastamento] = useState(null);
  const [tooltipMotivoRetorno, setTooltipMotivoRetorno] = useState(null);
  const [checkMPDisabled, setCheckMPDisabled] = useState(true);

  const motivoAfastamentoToMotivoRetorno = {
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE.value]: RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE.value,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA.value]: RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q4.value,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_1ANO.value]: RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q5.value,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_4ANOS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q6.value,
    [AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_SUP_15_DIAS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_ACIDENTE_TRABALHO_MAIOR_15_DIAS.value,
    [NOVO_AFASTAMENTO_MESMO_ACIDENTE.value]: RETORNO_AFASTAMENTO_TEMPORARIO_MESMO_ACIDENTE_TRABALHO.value,
    [AFASTAMENTO_TEMP_SERVICO_MILITAR.value]: RETORNO_AFASTAMENTO_TEMPORARIO_SERVICO_MILITAR.value,
    [AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_INF_15DIAS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_ACIDENTE_TRABALHO_IGUAL_MENOR_15_DIAS.value,
    [NOVO_AFASTAMENTO_TEMP_MESMA_DOENCAO_DENTRO_60DIAS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_MESMA_DOENCA.value,
    [LICENCA_PERCEPCAO_SALARIO.value]: RETORNO_LICENCA_COM_PERCEPCAO_SALARIO.value,
    [LICENCA_SEM_PERCEPCAO_SALARIO.value]: RETORNO_LICENCA_SEM_PERCEPCAO_SALARIO.value,
    [APOSENTADORIA_INVALIDEZ.value]: RETORNO_APOSENTADORIA_INVALIDEZ.value,
    [AFASTAMENTO_TEMP_MANDATO_SINDICAL.value]: RETORNO_AFASTAMENTO_TEMPORARIO_MANDATO_SINDICAL.value,
    [AFASTAMENTO_TEMP_ABORTO_NAO_CRIMINOSO.value]: RETORNO_AFASTAMENTO_TEMPORARIO_ABORTO_NAO_CRIMINOSO_Q3.value,
    [PRORROGACAO_AFASTAMENTO_TEMP_LICENCA_MATERNIDADE.value]: RETORNO_PRORROGACAO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE.value,
    [AFASTAMENTO_TEMP_DOENCA_PER_IG_INF_15DIAS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_DOENCAO_IGUAL_MENOR_15_DIAS.value,
    [AFASTAMENTO_SUSPENSAO_CONTRATO_TRABALHO.value]: RETORNO_AFASTAMENTO_SUSPENSAO_CONTRATO_TRABALHO.value,
    [PRORROGACAO_DURACAO_LICENCA_MATERNIDADE.value]: RETORNO_AFASTAMENTO_TEMPORARIO_LICENCA_MATERNIDADE_Q7.value,
    [AFASTAMENTO_TEMP_DOENCA_PER_SUP_15DIAS.value]: RETORNO_AFASTAMENTO_TEMPORARIO_DOENCA_MAIOR_15_DIAS.value,
    [SUSPENSAO_TEMPORARIA_DO_CONTRATO_TRABALHO_MP936_20.value]: RETORNO_SUSPENSAO_TEMPORARIA_DO_CONTRATO_TRABALHO_MP936_20.value,
  };

  const dataAfastamentoToDataRetorno = {
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE.value]: 120,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA.value]: 120,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_1ANO.value]: 60,
    [AFASTAMENTO_TEMP_LICENCA_MATERNIDADE_ADOCAO_GUARDA_SUP_4ANOS.value]: 30,
  };

  const afastamentosAtestadoObrigatorio = [
    AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_SUP_15_DIAS,
    AFASTAMENTO_TEMP_ACIDENTE_TRAB_PER_INF_15DIAS,
    AFASTAMENTO_TEMP_DOENCA_PER_IG_INF_15DIAS,
    AFASTAMENTO_TEMP_DOENCA_PER_SUP_15DIAS,
    NOVO_AFASTAMENTO_MESMO_ACIDENTE,
    NOVO_AFASTAMENTO_TEMP_MESMA_DOENCAO_DENTRO_60DIAS,
  ];

  const afastamentosDoenca = [
    AFASTAMENTO_TEMP_DOENCA_PER_IG_INF_15DIAS,
    AFASTAMENTO_TEMP_DOENCA_PER_SUP_15DIAS,
    NOVO_AFASTAMENTO_TEMP_MESMA_DOENCAO_DENTRO_60DIAS,
  ];

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'nroOrdem' });

        const value = formProps.getValues();

        if (isEmpty(value.nroOrdem)) {
          const parentValues = props.parentFormProps.getValues();
          if (parentValues.movimentacoes.length > 0) {
            formProps.setValue('nroOrdem', getItemAtual(parentValues.movimentacoes).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }

        if (props.selectedItem && !isEmpty(props.selectedItem.id)) {
          checkSituacaoAfastamento();
          changeMotivoAfastamento(props.selectedItem.motivoAfastamento);
          ajustarDetalhes();
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const checkSituacaoAfastamento = () => {
    const vinculo = props.parentFormProps.getValues();
    const tipoEvento = EnumUtil.contains(SEM_VINCULO_ESOCIAL_VALUES, vinculo.categoria)
      ? EVT_NP_TRAB_SEM_VINC_ALTERACAO.value
      : EVT_NP_ALTERACAO_CONTRATUAL.value;

    verificarSituacaoAfastamento(
      {
        empresaInativa: globalReducer.globalParameter.empresa.inativa,
        idEmpresaMFP: globalReducer.globalParameter.empresaMFP.id,
        idMovimentacao: props.selectedItem.id,
        idVinculo: vinculo.id,
        tipoEvento,
      },
      {
        thenFunction: res => {
          setPodeGerarEventoPendente(res);
        },
      }
    );
  };

  const verificarMotivo = () => {
    const values = formProps.getValues();
    if (EnumUtil.contains(afastamentosAtestadoObrigatorio, values.motivoAfastamento)) {
      formProps.setValue('observacao', null);
      setModoTela('atestado');
    } else if (values.motivoAfastamento === LICENCA_SEM_PERCEPCAO_SALARIO.value) {
      formProps.setValue('atestado', null);
      setModoTela('observacao');
    } else {
      formProps.setValue('observacao', null);
      formProps.setValue('atestado', null);
      setModoTela('normal');
    }

    const containsMP = EnumUtil.contains(afastamentosDoenca, values.motivoAfastamento) === false;
    setCheckMPDisabled(containsMP);
    if (containsMP) {
      formProps.setValue('mp93620', false);
    }
  };

  const ajustarMotivoRetorno = () => {
    const values = formProps.getValues();

    if (!isEmpty(values.dataAfastamento) && Object.keys(motivoAfastamentoToMotivoRetorno).indexOf(values.motivoAfastamento) !== -1) {
      const motivo = motivoAfastamentoToMotivoRetorno[values.motivoAfastamento];

      if (Object.keys(dataAfastamentoToDataRetorno).indexOf(values.motivoAfastamento) !== -1) {
        formProps.setValue(
          'dataAfastamento',
          formatDate(add(toDate(values.dataAfastamento), dataAfastamentoToDataRetorno[values.motivoAfastamento], 'minutes'))
        );
      }

      formProps.setValue('motivoRetorno', motivo);
      changeMotivoRetorno(motivo);
    }
  };

  const ajustarDetalhes = () => {
    const values = formProps.getValues();
    formProps.setValue([
      { dataAfastamentoFmt: values.dataAfastamento },
      { dataFimAfastamentoFmt: isEmpty(values.dataRetorno) ? null : formatDate(add(toDate(values.dataRetorno), -1, 'day')) },
      { dataRetornoFmt: values.dataRetorno },
    ]);
  };

  const changeMotivoAfastamento = value => {
    setTooltipMotivoAfastamento(null);

    const values = formProps.getValues();

    if (value) {
      changeMotivoRetorno(values.motivoRetorno);

      setTooltipMotivoAfastamento({
        message: EsocialUtil.getToolTipFmt(EnumUtil.find(AFASTAMENTO_ENUM_VALUES, value)),
      });

      if (values.dataRetorno) {
        ajustarMotivoRetorno();
      }
    }

    verificarMotivo();
  };

  const changeMotivoRetorno = value => {
    if (value) {
      setTooltipMotivoRetorno({
        message: EsocialUtil.getToolTipFmt(EnumUtil.find(AFASTAMENTO_ENUM_VALUES, value)),
      });
    }
    verificarMotivo();
  };

  const changeDataRetorno = () => {
    ajustarMotivoRetorno();
    ajustarDetalhes();
  };

  const changeDataAfastamento = () => {
    ajustarDetalhes();
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData.dataAfastamento) && !isEmpty(formData.dataRetorno) && compare(formData.dataRetorno, formData.dataAfastamento) <= 0) {
      erros.push('Data de retorno não pode ser inferior ou igual a data de afastamento');
    }

    if (!isEmpty(formData.dataAfastamento) && !isEmpty(parentData.recisao) && !isEmpty(parentData.recisao.dtAfastamento)) {
      if (compare(formData.dataAfastamento, parentData.recisao.dtAfastamento) > 0) {
        erros.push('Data de Afastamento deve ser anterior a data de Aviso Prévio.');
      }
    }

    if (isEmpty(parentData.dataAdmissao)) {
      erros.push('Não é permitido cadastrar afastamento sem data de admissão');
    }

    if (compare(parentData.dataAdmissao, formData.dataAfastamento) === 1) {
      erros.push('Data de afastamento não pode ser inferior à data de admissão');
    }

    if (isEmpty(formData.dataRetorno) && parentData.movimentacoes && parentData.movimentacoes.length > 0) {
      if (getItemAtual(parentData.movimentacoes).nroOrdem > formData.nroOrdem) {
        erros.push('Data do retorno não pode estar em branco para esse registro');
      }
    }

    if (!isEmpty(formData.dataRetorno) || !isEmpty(formData.motivoRetorno)) {
      if (isEmpty(formData.dataRetorno)) {
        erros.push('Data de retorno não pode ficar em branco');
      }
      if (isEmpty(formData.motivoRetorno)) {
        erros.push('Motivo do retorno não pode ficar em branco');
      }
    }

    if (parentData.movimentacoes) {
      parentData.movimentacoes.forEach(item => {
        if (item.nroOrdem !== formData.nroOrdem && !isEmpty(item.dataAfastamento) && !isEmpty(item.dataRetorno)) {
          if (!isEmpty(formData.dataAfastamento)) {
            if (compare(formData.dataAfastamento, item.dataAfastamento) >= 0 && compare(formData.dataAfastamento, item.dataRetornoFmt) < 0) {
              erros.push('Data de afastamento não pode estar num período de afastamento já cadastrado');
            }
          }
          if (!isEmpty(formData.dataRetorno)) {
            if (compare(formData.dataRetorno, item.dataAfastamento) > 0 && compare(formData.dataRetorno, item.dataRetorno) <= 0) {
              erros.push('Data de retorno não pode estar num período de afastamento já cadastrado');
            }
          }
          if (!isEmpty(formData.dataAfastamento) && !isEmpty(formData.dataRetorno)) {
            const checkA = compare(item.dataAfastamento, formData.dataAfastamento) >= 0 && compare(item.dataAfastamento, formData.dataRetorno) < 0;
            const checkB = compare(item.dataRetorno, item.dataAfastamento) > 0 && compare(item.dataRetorno, item.dataRetorno) <= 0;
            if (checkA && checkB) {
              erros.push('Já existe afastamento cadastrado pertencente ao período de afastamento e retorno informado');
            }
          }
        }
      });
    }

    if (formData.motivoAfastamento === AFASTAMENTO_TEMP_MANDATO_SINDICAL.value) {
      const msg = "Para o motivo de afastamento selecionado : 'Afastamento temporário para exercício de mandato sindical'";
      if (!isEmpty(parentData.sindicato)) {
        erros.push(
          msg +
            ", é necessário que o Vinculo possua um Sindicato e que no cadastro do sindicato seja preenchida a informação ' +" +
            "complementar do mandato sindical. Acesse Manutenção > Vinculo > Selecione o Vinculo > Aba 'Geral'"
        );
      } else if (isEmpty(parentData.sindicato.onusRemuneracaoAfast)) {
        erros.push(
          msg +
            ", é necessário que seja prenchida a informação complementar do mandato sindical. '" +
            "Acesse Manutenção > Tabelas > Sindicato > Selecione o Sindicato > Aba 'Mandato Sindical'"
        );
      }
    }

    if (formData.motivoAfastamento === LICENCA_SEM_PERCEPCAO_SALARIO.value && isEmpty(formData.observacao)) {
      erros.push(
        "Para o motivo de afastamento selecionado : 'X2 / 21 - Licença sem percepção de salário'  é necessário que o campo " +
          'observação seja preenchido com as informações sobre o afastamento,  de maneira a explicitar os motivos do mesmo.'
      );
    }

    if (formData.motivoAfastamento === SUSPENSAO_TEMPORARIA_DO_CONTRATO_TRABALHO_MP936_20.value && isEmpty(formData.dataRetorno)) {
      erros.push('"Para o motivo de afastamento selecionado é obrigatório informar uma data de retorno');
    }

    return erros;
  };

  const handleSubmit = (data, validateExtrasFunc) => {
    if (data.id && data.dataRetorno && podeGerarEventoPendente) {
      promisedAlert('Deseja gerar o evento pendente de retorno do afastamento para o eSocial?').then(() => {
        // TODO implementar a geração de eventos pendentes
        props.handleSubmit(data, validateExtrasFunc);
      });
    } else {
      props.handleSubmit(data, validateExtrasFunc);
    }
  };

  return (
    <DetailEditForm handleSubmit={handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="dataAfastamento"
            label="Data do Afastamento"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
            onBlur={changeDataAfastamento}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="motivoAfastamento"
            label="Motivo do Afastamento"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: MOTIVO_AFASTAMENTO_VALUES }}
            type={InputType.DROP_DOWN}
            placeholder="Selecione o Motivo do Afastamento"
            onChange={changeMotivoAfastamento}
            tooltipProps={tooltipMotivoAfastamento}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dataRetorno"
            label="Data do Retorno"
            formProps={formProps}
            isPrependLabel={true}
            type={InputType.DATE}
            tooltipProps={{ message: 'Data em que o vínculo retornou ao trabalho.' }}
            onBlur={changeDataRetorno}
          />
        </Col>
      </Row>
      <div className="d-flex">
        <div className="flex-grow-1">
          <InputGroupInline
            id="motivoRetorno"
            label="Motivo do Retorno"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: MOTIVO_RETORNO_VALUES }}
            type={InputType.DROP_DOWN}
            placeholder="Selecione o Motivo do Retorno"
            onChange={changeMotivoRetorno}
            tooltipProps={tooltipMotivoRetorno}
          />
        </div>
        <div style={{ width: 30, marginLeft: 10, paddingTop: 7 }}>
          <Button
            id="btnObterMotivoRetorno"
            color="neutral-success"
            className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--success"
            onClick={ajustarMotivoRetorno}
          >
            <span className="btn-wrapper--icon">
              <FontAwesomeIcon icon={['fas', 'sync']} />
            </span>
          </Button>
          <UncontrolledTooltip target="btnObterMotivoRetorno" placement="bottom">
            Obter motivo retorno correspondente ao afastamento.
          </UncontrolledTooltip>
        </div>
      </div>
      {condicional(
        modoTela === 'atestado',
        <div className="d-flex">
          <div className="flex-grow-1">
            <AtestadoIS
              id="atestado"
              formProps={formProps}
              label="Atestado Médico"
              isPrependLabel={true}
              tooltipProps={{ message: 'Informações complementares relativas ao atestado médico.' }}
            />
          </div>
          <div style={{ width: 230, marginLeft: 10, marginTop: 10 }}>
            <Checkbox
              id="mp93620"
              formProps={formProps}
              label="COVID-19 (MPV-936/20)"
              tooltipProps={{
                message: 'Desconsiderar 15 dias por parte da empresa em função de infermidade COVID-19, Lei nº 13.982 2 DE ABRIL DE 2020',
              }}
              labelProps={{ style: { color: 'red' } }}
              disabled={checkMPDisabled}
            />
          </div>
        </div>
      )}
      {condicional(
        modoTela === 'observacao',
        <Row>
          <Col>
            <InputGroupInline
              id="observacao"
              label="Observação"
              formProps={{ ...formProps, validation: { maxLength: 255 } }}
              isPrependLabel={true}
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      )}
      <Box title="Detalhes">
        <Row>
          <Col>
            <InputGroupInline
              id="dataAfastamentoFmt"
              label="Data de início do Afastamento"
              formProps={formProps}
              isPrependLabel={true}
              type={InputType.DATE}
              disabled={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataFimAfastamentoFmt"
              label="Data de fim do Afastamento"
              formProps={formProps}
              isPrependLabel={true}
              type={InputType.DATE}
              disabled={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataRetornoFmt"
              label="Data de retorno ao Trabalho"
              formProps={formProps}
              isPrependLabel={true}
              type={InputType.DATE}
              disabled={true}
            />
          </Col>
        </Row>
      </Box>
    </DetailEditForm>
  );
};

export default VinculoMovimentacaoEditView;
