import React, { FC, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  Box,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditFormRefProps,
  EditViewProps,
  enumColumn,
  getSelectedItem,
  InputGroupInline,
  InputType,
  onlyNumber,
  OrderType,
  ProcessBar,
  WindowComponent,
  Wizard,
  WizardActionFinishProps,
  WizardPage,
} from 'summer';
import { CLASS_NAME, Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { MOTIVO_AFASTAMENTO_VALUES, MOTIVO_RETORNO_VALUES } from '../../../../models/enumerated/mfp/afastamentoEnum';
import { PERCENTUAL_REDUCAO_CARGA_HORARIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/percentualReducaoCargaHorariaEnum';
import { REDUCAO_CARGA_HORARIA, TIPO_ADESAO_BEM_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoAdesaoBEMEnum';
import { condicional, loadVinculo, registrarListasVinculo } from '../../../../utilities/vinculo.util';
import TrabalhadorIS from '../../../components/mfp/trabalhadorIS/TrabalhadorIS';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import VinculoMovimentacaoEditView from './VinculoMovimentacaoEditView';

interface ScreenFields {
  vinculo?: Vinculo;
}

interface AfastamentoEditViewProps extends EditViewProps<Vinculo> {
  onDockSubmit?: (data: any) => void;
  openViaDock?: boolean;
}

const AfastamentoEditView: FC<AfastamentoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Vinculo & ScreenFields>({ defaultValues: { salarios: [], ...selectedItem }, mode: 'onBlur' });
  const dispatch = useDispatch();

  const editFormRef = useRef<EditFormRefProps>();
  const [isReducaoCargaHoraria, setIsReducaoCargaHoraria] = useState(false);

  useEffect(
    () => {
      setTimeout(() => {
        // Registra todos os parametros do vinculo necessários para que a persistencia seja executada corretamente
        registrarListasVinculo(formProps, ['movimentacoes'], ['dataAdmissao', 'categoria', 'sindicato', 'recisao']);
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const tipoAdesaoChange = value => {
    if (value) {
      setIsReducaoCargaHoraria(value === REDUCAO_CARGA_HORARIA.value);
      if (!isReducaoCargaHoraria) {
        formProps.setValue('percentualReducaoCargaHoraria', 0);
      }
    } else {
      formProps.setValue([{ percentualReducaoCargaHoraria: 0 }, { dtAcordoBEM: null }, { diasDuracaoAcordo: 0 }]);
    }
  };

  const onPageChange = currentPageId => {
    if (currentPageId === 'afastamento') {
      loadVinculo(formProps, dispatch);
    }
  };

  const movimentacaoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'nroOrdem',
      order: OrderType.ASC,
    },
  ];

  const movimentacaoFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'dataAfastamento',
        text: 'Data do Afastamento',
      },
      {
        dataField: 'dataRetorno',
        text: 'Data do Retorno',
      },
      enumColumn(
        {
          dataField: 'motivoAfastamento',
          text: 'Motivo do Afastamento',
        },
        MOTIVO_AFASTAMENTO_VALUES
      ),
      enumColumn(
        {
          dataField: 'motivoRetorno',
          text: 'Motivo do Retorno',
        },
        MOTIVO_RETORNO_VALUES
      ),
    ],
    detailView: VinculoMovimentacaoEditView,
    detailViewSize: 'large',
    detailViewWikiId: 1480,
    formProps,
    propName: 'movimentacoes',
    title: 'Histórico de afastamentos',
  };

  const reset = () => {
    formProps.setValue('vinculo', null);
  };

  const onCancel = () => {
    editFormRef.current.close(true);
  };

  const wizardFinishProps: WizardActionFinishProps = {
    confirmButton: {
      label: 'Salvar',
    },
    onCancel: () => {
      reset();
      onCancel();
    },
    onConfirm: () => {
      editFormRef.current.submit().then(reset);
    },
  };

  const getMainEditView = () => (
    <>
      <Row>
        <Col>
          <div className="d-flex">
            <div style={{ width: 240 }}>
              <InputGroupInline formProps={formProps} isPrependLabel={true} id="matricula" label="Matrícula" disabled={true} />
            </div>
            <div className="flex-grow-1" style={{ marginLeft: 20 }}>
              <TrabalhadorIS id="trabalhador" formProps={formProps} isPrependLabel={true} label="Trabalhador" disabled={true} />
            </div>
          </div>
        </Col>
      </Row>
      <Row>
        <Col style={{ marginTop: 40 }}>
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={movimentacaoDefaultSortProps}
            {...movimentacaoFormProps}
            keyField="rowIndex"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Box title="B.E.M. MPV-936/20" wikiId={7244}>
            <Row>
              <Col>
                <InputGroupInline
                  id="tipoAdesaoBEM"
                  label="Tipo de Adesão"
                  formProps={formProps}
                  isPrependLabel={true}
                  inputDropDownProps={{ optionsProvider: TIPO_ADESAO_BEM_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                  placeholder="Selecione o Tipo de Adesão"
                  tooltipProps={{ message: 'Tipo de adesão acordado entre o empregador e o empregado' }}
                  onChange={tipoAdesaoChange}
                />
              </Col>
              {condicional(
                isReducaoCargaHoraria,
                <Col>
                  <InputGroupInline
                    id="percentualReducaoCargaHoraria"
                    label="Redução carga horária"
                    formProps={formProps}
                    isPrependLabel={true}
                    inputDropDownProps={{ optionsProvider: PERCENTUAL_REDUCAO_CARGA_HORARIA_ENUM_VALUES }}
                    type={InputType.DROP_DOWN}
                    placeholder="Selecione o percentual de redução da carga horária"
                    tooltipProps={{ message: 'Redução carga horária acordada entre o empregador e o empregado' }}
                  />
                </Col>
              )}
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="dtAcordoBEM"
                  label="Data do Acordo"
                  formProps={formProps}
                  isPrependLabel={true}
                  type={InputType.DATE}
                  tooltipProps={{ message: 'Data em que foi firmado o acordo entre o empregador e o empregado' }}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="diasDuracaoAcordo"
                  label="Dias de duração"
                  formProps={{ ...formProps, validation: { ...onlyNumber(0, 2), min: 0, max: 90 } }}
                  isPrependLabel={true}
                  type={InputType.NUMBER}
                  tooltipProps={{ message: 'Data em que foi firmado o acordo entre o empregador e o empregado' }}
                />
              </Col>
            </Row>
          </Box>
        </Col>
      </Row>
    </>
  );

  const handleSubmitViaDock = () => {
    editFormRef.current.submit(false).then(res => {
      editFormRef.current.close();
      if (props.onDockSubmit) {
        props.onDockSubmit(res);
      }
    });
  };

  const getFooter = () => <ProcessBar handleSubmit={handleSubmitViaDock} handleClose={onCancel} btnSubmitIcon="save" btnSubmitLabel="Salvar" />;

  return (
    <WindowComponent title="Afastamentos - MPV-936/20" wrapBody={props.openViaDock === true} wikiId={1480}>
      <EditForm ref={editFormRef} formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} externalSubmit={true}>
        {props.openViaDock === true ? (
          <div className="m-3">
            {getMainEditView()}
            {getFooter()}
          </div>
        ) : (
          <Wizard formProps={formProps} onAfterPageChange={onPageChange} finishProps={wizardFinishProps}>
            <WizardPage id="vinculo" title="Seleção do vínculo">
              <Row>
                <Col>
                  <VinculoIS id="vinculo" formProps={{ ...formProps, validation: { required: true } }} label="Vinculo" isPrependLabel={true} />
                </Col>
              </Row>
            </WizardPage>
            <WizardPage id="afastamento" title="Afastamentos">
              {getMainEditView()}
            </WizardPage>
          </Wizard>
        )}
      </EditForm>
    </WindowComponent>
  );
};

export default AfastamentoEditView;
